



.container {
  position: relative;
  border: 1px solid rgb(131, 131, 131);
  margin: 1em;
  padding: 1em;
  display: flex;
  flex: 1;
}

.demo {
  margin: 4em 0;
}

.container-title {
  font-size: 0.8em;
  font-weight: bold;
  position: absolute;
  margin-top: -1.2em;
  top: 0;
  left: 0.5em;

  background-color: #282c34;
  padding: 0.5em;
  color: rgb(171, 171, 171);
  text-transform: uppercase;
}

.container-info {
  font-size: 0.8em;
  position: absolute;
  margin-top: -1.2em;
  top: 0;
  right: 0.5em;

  background-color: #282c34;
  padding: 0.5em;
  color: rgb(171, 171, 171);
}
  
.row {
  display: flex;
  flex-direction: row;
}

.column {
  display: flex;
  flex-direction: column;
}

.desktop-right {
  justify-content: flex-end;
}

@media only screen and (max-width: 600px) {
  .row:not(.forced) {
    flex-direction: column;
  }

  .container ~ .container {
    margin-top: 0.5em;
  }

  .desktop-right {
    justify-content: center;
  }
}

.button {
  background: #1d8cf8;
  background-image: linear-gradient(to bottom left,#1d8cf8,#3358f4,#1d8cf8);
  background-size: 210% 210%;
  background-position: 100% 0;
  color: #fff;
  box-shadow: 2px 2px 6px rgb(0 0 0 / 40%);
  text-align: center;
  padding: 8px 17px;
  font-weight: 400;
  border-radius: 0.25rem;
  border: none;
  margin: 1em;
  cursor: pointer;
}
